import React, { Component } from 'react';
import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl';
class Contact extends Component {
    render() {
        return(
            <div className="contact-body">
                <Grid className="contact-grid">
                    <Cell coll={6}>
                        <h2>Mohit R Chugh</h2>
                        <img 
                           src="https://cdn2.iconfinder.com/data/icons/avatar-2/512/Fred_man-512.png"
                           alt="avatar"
                           style={{height: '200px'}}
                           />
                        <p style={{width: '100%',margin:'auto',paddingTop: '1em'}}>Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                           Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,
                           when an unknown printer took a galley of type and scrambled it to make a type
                           specimen book.</p>
                    </Cell>
                    <Cell coll={6} style={{marginLeft: '15%'}}>
                        <h2>Contact Me</h2>
                        <hr/>

                    <div className="contact-list">
                    <List>
                <ListItem>
                  <ListItemContent style={{fontSize: '18px', fontFamily: 'Anton'}}>
                    <i className="fa fa-phone-square" aria-hidden="true"/>
                    (91) 7795204961
                  </ListItemContent>
                </ListItem>

                <ListItem>
                  <ListItemContent style={{fontSize: '18px', fontFamily: 'Anton'}}>
                    <i className="fa fa-envelope" aria-hidden="true"/>
                    mohit.jan92@gmail.com
                  </ListItemContent>
                </ListItem>

                <ListItem>
                  <ListItemContent style={{fontSize: '18px', fontFamily: 'Anton'}}>
                    <i className="fa fa-skype" aria-hidden="true"/>
                    MySkypeID
                  </ListItemContent>
                </ListItem>


              </List>
                    </div>
                    
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Contact;